@import url('https://fonts.googleapis.com/css2?family=Roboto:wght@400;900&display=swap');
@import 'middleBlock.scss';
@import 'rightBlock.scss';
body {
	margin: 0;
	font-family: 'Roboto', sans-serif;
}

.main {
	display: flex;
	justify-content: space-between;
	height:100vh;
	border: 1px solid rgba(0, 0, 0, 1);
	box-sizing: border-box;
}

.main a {
	color: rgba(0, 0, 255, 1);
	cursor: pointer;
}

.leftBlock {
	display: flex;
	flex-direction: column;
	//	width: 27%;
	width: 30.1%;
	min-width: 300px;
	justify-content: space-between;
	&__search {
		display: flex;
		background-color: #CBCBCB;
		box-sizing: border-box;
		//padding:30px;
		//padding-top: 29px;
		//margin:auto;
		//height:11%;
		justify-content: space-between;
		margin-top: 0;
		flex:1 1 auto;
	}
	&__search-content {
		height: 39px;
		padding: 0;
		margin:30px;
	}
	&__search-input {
		height: 100%;
		box-sizing: border-box;
		font-size: 24px;
		width: 57%;
	}
	&__search-button {
		height: 100%;
		font-size: 24px;
		//outline: none;
		background-color: #3F50B5;
		color: #FFFFFF;
		border:none;
		width: 24%;
		margin-left: 19px;
		cursor: pointer;
	}
	&__list {
		height: auto;
		overflow-y: scroll;
		min-height: 78vh;
		border: 1px solid rgba(0, 0, 0, 1);
		box-sizing: border-box;
	}
	&__list::-webkit-scrollbar {
  width: 0;
	}
	&__item {
		border: 1px solid rgba(0, 0, 0, 1);
		padding-left: 24px;
		padding-top: 24px;
		padding-right: 27px;
		min-height:125px;
	}
	&__item-title {
		font-size: 24px;
		cursor: pointer;
	}
	&__item-subtitle {
		font-size: 18px;
	}
	&__pages {
		height: 57px;
		background-color: #CBCBCB;
		box-sizing: border-box;
		font-size: 12px;
		display: flex;
		flex-direction: column;
		padding-bottom: 14px;
	}
	&__pages-counter {
		display:flex;
		justify-content: space-between;
		margin-left: 24px;
		max-width: 232px;
		visibility: hidden;
	}
	&__pages-buttons {
		margin-left: 24px;
		//margin-right: 225px;
		display: flex;
		//justify-content: space-between;
		visibility: hidden;
	}
}
#next {
	margin-left: 18px;
}
.active {
	background-color: #EEEEEE;
}
.active .leftBlock__item-title {
	font-weight: 900;
	font-size: 24px;
}
.visible {
	visibility: visible;
}