@import url('https://fonts.googleapis.com/css2?family=Roboto:wght@400;900&display=swap');
.middleBlock {
	display: flex;
	margin-left: 37px;
	margin-right: 32px;
	margin-top: 29px;
	flex:1;
	flex-direction: column;
	font-family: 'Roboto';
	font-weight: 400;
	font-size: 24px;
	width: 43.3%;
	word-wrap: break-word;
	height: auto;
	overflow-y: scroll;
	visibility: hidden;
	&__info * {
		margin-top:12px;
		margin-bottom: 0;
	}

	&__info-title {
		font-size: 36px;
		margin:0;
	}
	&__info-subtitle {
		margin-top: 13px;
		color: rgba(171, 171, 171, 1);
	}
	&__info-lang {
		margin-top: 37px;
	}

}
.middleBlock::-webkit-scrollbar {
 	width: 0;
}
.middleBlock-button {
	margin-top: 61px;
	text-decoration:none;
}

.visible {
	visibility: visible;
}