@import url('https://fonts.googleapis.com/css2?family=Roboto:wght@400;900&display=swap');
.rightBlock {
	display: flex;
	flex-direction: column;
	width: 26.6%;
	min-width: 300px;
	font-family: 'Roboto';
	font-weight: 400;
	border: 1px solid rgba(0, 0, 0, 1);
	box-sizing: border-box;
	padding: 0;
	&__head {
		background-color: #C4C4C4;
		padding-top: 21px;
		padding-bottom: 13px;
		padding-left: 24px;
	}
	&__head-title {
		font-size: 36px;
		margin:	 0;
	}
	&__head-info {
		font-size: 18px;
		margin-top: 4px;
	}
}

.rightBlock-list {
	height: auto;
	overflow-y: scroll;
	&__item {
		box-sizing: border-box;
		border: 1px solid rgba(0, 0, 0, 1);
		padding-left: 23px;
		padding-bottom: 17px;
		padding-top: 24.4px;
		min-height: 175px;
	}
	&__item * {
		margin:0;
		padding:auto;
	}
	&__item-title {
		font-size: 24px;
	}
	&__item-subtitle {
		color: rgba(171, 171, 171, 1);
		font-size: 18px;
	}
	&__item-author {
		margin-top: 2.6px;
		font-size: 24px;
	}
	&__item-buttons {
		font-size: 18px;
		margin-top: 44px;
	}
	&__item-remove {
		margin-left: 26px;
	}

}

.rightBlock-list::-webkit-scrollbar {
 	width: 0;
}
.mark {
	min-height: 0;
}
.mark a {
	visibility: hidden;
}
.mark * {
	color:green;
}